import Topbar from '../../components/topBar/topbarx.vue'
export default {
	data() {
		return {
			title: '门票'
		};
	},
	methods: {
		childbar(val) {
			switch (val.identify) {
				case "leftHandle":
					break;
				case "rightHandle":
					this.$router.push({
						path: "/pwdreg",
						query: {
							tag: "reg",
							btn: "reg"
						}
					});
					break;
			}
		},
	},
	components: {
		Topbar
	}
}
